export const enum BwSearchArea { city, region, country }
export enum BwSearchRealty { Квартиры, Комнаты, Загородная, Коммерческая, Гаражи, Доли }
export interface BwSearchOrder {
  id: string,
  title: string,
  area: BwSearchArea,
  realty: BwSearchRealty,
}

export class $bw_search extends $.$bw_search {
  static instance() {
    const result = ($bw_app.Root(0) as $bw_app).NewLook().Workspace().Search() as $bw_search
    return result
  }

// WARNING: newGuid to be replaced for something like https://www.npmjs.com/package/guid-typescript
  static newGuid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
        return v.toString(16);
    });
  }
  new_order(area: BwSearchArea, realty: BwSearchRealty) {
    const orders_old = this.orders()
    const orders_new = [...orders_old]
    const id = $bw_search.newGuid()
    const title = this.new_order_title_default()
    orders_new.unshift({id, title, area, realty})
    this.orders(orders_new)
    this.selected_order_id(id)
  }
  new_order_title_default() {
    const orders = this.orders()
    const titles = new Set(orders.map((order: BwSearchOrder) => order.title.toLowerCase()))
    const title_default = (n: number) => `Заказ ${n}`
    let n = 1
    let result = title_default(n)
    while (titles.has(result.toLowerCase())) {
      result = title_default(++n)
    }
    return result
  }
  @ $bw_local
  orders(val?: BwSearchOrder[]) {
    const result = super.orders(val) as BwSearchOrder[]
    // console.log(result)
    return result
  }
  orders_equal(a: BwSearchOrder[], b: BwSearchOrder[]) {
    return (!a || !a.length) && (!b || !b.length)
  }
  orders_restore(val_store: any) {
    let result = (
      !Array.isArray(val_store) ? [] :
        val_store
        .filter((item: BwSearchOrder) =>
          item &&
          typeof item == 'object' &&
          typeof item.id == 'string' &&
          item.id &&
          typeof item.title == 'string' &&
          typeof item.area == 'number' &&
            BwSearchArea.city <= item.area && item.area <= BwSearchArea.country &&
          typeof item.realty == 'number' &&
            BwSearchRealty.Квартиры <= item.realty && item.realty <= BwSearchRealty.Доли &&
          true
        )
    ) as BwSearchOrder[]
    // console.log(result)
    return result
  }
  @ $mol_mem
  menu_options() {
    const result = this.orders().reduce((accu: {}, order: BwSearchOrder) => ({...accu, [order.id]: order.title}), { '': 'Новый заказ'})
    return result
  }

  @ $bw_session
  selected_order_id( val? : any , force? : $mol_atom_force) {
    return super.selected_order_id(val, force)
  }
  content() {
    const result = this.selected_order_id() === '' ? this.New() : this.Workspace()
    return result
  }
  menu_item_close_click(id: string, event?: any) {
    const orders = this.orders()
    let idx = orders.findIndex(order => order.id == id)
    if (idx >= 0) {
      const orders_new = [...orders]
      orders_new.splice(idx, 1)
      this.orders(orders_new)
      let id_new = this.selected_order_id()
      if (id == id_new) {
        if (idx > 0) idx -= 1
        id_new = idx >= orders_new.length ? '' : orders_new[idx].id
      }
      new $mol_defer(() =>
        this.selected_order_id(id_new)
      )
    }
  }
}

