interface BwSearchBlockTableDataItem {
  realty: BwSearchRealty,
  count_all: number,
  count_today: number,
  count_new: number,
}

export class $bw_search_block_table extends $.$bw_search_block_table {
  rows() {
    const table_data = this.table_data()
    const result = !Array.isArray(table_data) ? null : table_data.map((row_data: BwSearchBlockTableDataItem) => this.Row(row_data))
    return result
  }
  realty(row_data: BwSearchBlockTableDataItem|null) {
    const result = !row_data ? '' : BwSearchRealty[row_data.realty]
    return result
  }
  count_all(row_data: BwSearchBlockTableDataItem|null) {
    return !row_data ? '' : this.format_number(row_data.count_all)
  }
  count_today(row_data: BwSearchBlockTableDataItem|null) {
    return !row_data ? '' : this.format_number(row_data.count_today)
  }
  count_new(row_data: BwSearchBlockTableDataItem|null) {
    return !row_data ? '' : this.format_number(row_data.count_new)
  }
  format_number(n: number) {
    const result = n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ");
    return result
  }
}

