export class $bw_search_params extends $.$bw_search_params {
  @ $bw_animate
  main_params_opacity() {
    const result = this.mode() === 'main' ? 1 : 0
    return result
  }
  @ $bw_conditional
  Main() {
    const result = super.Main()
    // console.warn(result)
    return result
  }
  Main_condition() {
    let mode: string
    let main_params_opacity_animation_is: boolean
    let main_params_opacity: number
    const result =
      (() => (mode = this.mode()) == 'main')() ||
      (() => main_params_opacity_animation_is = (this as any).main_params_opacity_animation_is())() ||
      (() => (main_params_opacity = this.main_params_opacity()) == 1)()
    // console.log({result, mode, main_params_opacity_animation_is, main_params_opacity})
    return result
  }

  @ $bw_animate
  full_params_opacity() {
    const result = this.mode() === 'full' ? 1 : 0
    return result
  }
  @ $bw_conditional
  Full() {
    const result = super.Full()
    return result
  }
  Full_condition() {
    const result =
      this.mode() == 'full' ||
      (this as any).full_params_opacity_animation_is() ||
      this.full_params_opacity() == 1
    return result
  }

  @ $bw_animate
  min_height() {
    const result = this.min_height_config()[this.mode()] || 0
    return result
  }
  min_height_animation_start(p: BwAnimateParam<number, number>) {
    const delta = p.end - p.start
    if (delta < 0) {
      let content_client_rect_height = $bw_search.instance().Result().content_client_rect_height()
      if (content_client_rect_height) {
        content_client_rect_height -= delta
        $bw_search.instance().Result().content_client_rect_height(content_client_rect_height)
      }
    }
  }
  min_height_animation_finish(p: BwAnimateParam<number, number>) {
    $bw_search.instance().Result().content_client_rect_height(null)
  }
  min_height_config() {
    return {full: 627 - 50, main: 295, short: 100}
  }
  min_height_animation_steps(p: BwAnimateParam<number, number>) {
    const config = this.min_height_config()
    const result = Math.sqrt(Math.abs(p.start - p.end)) / Math.sqrt(config['full'] - config['short']) * 16
    // console.log(result)
    // return 16
    return result
  }
  mode_enabled(id : string) {
    return id != this.mode()
  }
  @ $bw_session
  mode(val?: any, force?: $mol_atom_force) {
    return super.mode(val, force)
  }
  bread_crumb_defs() {
    return ['1-комн.кв', 'общая: 35-45м²', 'кухня: от 9м²', 'до 4.8 млн.₽']
    // return ['1-комн.кв', 'общая: 35-45м²', 'кухня: от 9м²']
  }
  bread_crumbs() {
    const result = this.bread_crumb_defs().map((def: any, i: number )=> this.BreadCrumb(i))
    return result
  }
  @ $bw_meter_key
  break_crumb_client_rect(i: number) {
    // https://stackoverflow.com/  questions/4866229/check-element-css-display-with-javascript
    const elem = this.BreadCrumb(i).dom_node() as HTMLElement
    const display = (elem as any).currentStyle ?
      (elem as any).currentStyle.display :
      getComputedStyle(elem, null).display
    const result = display == 'none' ? null :
      this.BreadCrumb(i).dom_node().getBoundingClientRect()
    // console.warn({i}, result)
    return result
  }
  client_rect() {
    return meter({
      _super: (val?: any) => super.client_rect(val),
      _value: () => {
        const result = this.dom_node().getBoundingClientRect()
        return result
      }
    })
  }
  input_client_rect() {
    return meter({
      _super: (val?: any) => super.input_client_rect(val),
      _value: () => {
        const result = this.Input().dom_node().getBoundingClientRect()
        return result
      }
    })
  }
  @ $mol_mem_key
  bread_crumb_display(i: number) {
    const result =
      i < this.bread_crumb_defs().length && (
        i == 0 || this.bread_crumb_pos(i - 1) !== null
      ) ?
      'block' : 'none'
    // console.log(i, result)
    return result
  }
  mode_client_rect() {
    return meter({
      _super: (val?: any) => super.mode_client_rect(val),
      _value: () => {
        const result = this.Mode().dom_node().getBoundingClientRect()
        return result
      }
    })
  }
  @ $mol_mem_key
  bread_crumb_pos(i: number) {
    let result: {left: number, top: number} = null
    if (i == 0) {
      const client_rect = this.client_rect()
      const input_client_rect = this.input_client_rect()
      if (client_rect && input_client_rect) {
        result = {
          top: input_client_rect.top - client_rect.top,
          left: input_client_rect.right - client_rect.left + 16,
        }
        // console.warn('bw_resize')
        window.dispatchEvent(new CustomEvent('bw_resize'))
      }
    } else if (i < this.bread_crumb_defs().length) {
      const client_rect = this.client_rect()
      const prev_client_rect = this.break_crumb_client_rect(i - 1)
      if (client_rect && prev_client_rect) {
        result = {
          top: prev_client_rect.top - client_rect.top,
          left: prev_client_rect.right - client_rect.left + 8,
        }
        window.dispatchEvent(new CustomEvent('bw_resize'))
        // console.warn({i}, result, prev_client_rect, client_rect)
      }
    }
    // console.warn({i}, result)
    return result
  }
  bread_crumb_top(i: number) {
    const pos = this.bread_crumb_pos(i)
    return pos === null ? null : pos.top
  }
  bread_crumb_left(i: number) {
    const pos = this.bread_crumb_pos(i)
    return pos === null ? null : pos.left
  }
  bread_crumb_text(i: number) {
    return this.bread_crumb_defs()[i]
  }
}

