namespace $ { export class $bw_input extends $mol_string {

	/// autofocus false
	autofocus() {
		return false
	}

	/// bw_autofocus false
	bw_autofocus() {
		return false
	}

	/// readonly false
	readonly() {
		return false
	}

	/// autocomplete \off
	autocomplete() {
		return "off"
	}

	/// bw_dropped_down false
	bw_dropped_down() {
		return false
	}

	/// minimal_height 32
	minimal_height() {
		return 32
	}

	/// attr *
	/// 	^
	/// 	autofocus <= autofocus
	/// 	readonly <= readonly
	/// 	autocomplete <= autocomplete
	/// 	bw_dropped_down <= bw_dropped_down
	/// 	bw_autofocus <= bw_autofocus
	attr() {
		return ({
			...super.attr() ,
			"autofocus" :  this.autofocus() ,
			"readonly" :  this.readonly() ,
			"autocomplete" :  this.autocomplete() ,
			"bw_dropped_down" :  this.bw_dropped_down() ,
			"bw_autofocus" :  this.bw_autofocus() ,
		})
	}

	/// plugins / <= Vim
	plugins() {
		return [].concat( this.Vim() )
	}

	/// Vim $bw_vim
	@ $mol_mem
	Vim() {
		return (( obj )=>{
			return obj
		})( new this.$.$$.$bw_vim )
	}

} }

