// https://github.com/epoberezkin/fast-deep-$bw_equal/blob/master/index.js
var isArray = Array.isArray;
var keyList = Object.keys;
var hasProp = Object.prototype.hasOwnProperty;

export type BwEqual<Value> = (a: Value, b: Value) => boolean
export function $bw_equal(a: any, b: any) : boolean {
  if (a === b) return true;

  if (a && b && typeof a == 'object' && typeof b == 'object') {
    var arrA = isArray(a)
      , arrB = isArray(b)
      , i
      , length
      , key;

    if (arrA != arrB) return false
    if (arrA && arrB) {
      length = a.length;
      if (length != b.length) return false;
      for (i = length; i-- !== 0;)
        if (!$bw_equal(a[i], b[i])) {
          const result = false
          // console.log({result}, a[i], b[i])
          return result
        }
      return true;
    }

    let setA = a instanceof Set
    let setB = b instanceof Set
    if (setA != setB) return false
    if (setA && setB) {
      if (a.size != b.size) return false
      let iter = a.keys()
      let next = iter.next()
      while (!next.done) {
        if (!b.has(next.value)) return false
        next = iter.next()
      }
      iter = b.keys()
      next = iter.next()
      while (!next.done) {
        if (!a.has(next.value)) return false
        next = iter.next()
      }
      return true
    }

    let mapA = a instanceof Map
    let mapB = b instanceof Map
    if (mapA != mapB) return false
    if (mapA && mapB) {
      const result = $bw_equal([...a], [...b])
      // console.log(result, [...a], [...b])
      return result
    }

    var dateA = a instanceof Date
      , dateB = b instanceof Date;
    if (dateA != dateB) return false;
    if (dateA && dateB) return a.getTime() == b.getTime();

    var regexpA = a instanceof RegExp
      , regexpB = b instanceof RegExp;
    if (regexpA != regexpB) return false;
    if (regexpA && regexpB) return a.toString() == b.toString();

    var keys = keyList(a);
    length = keys.length;

    if (length !== keyList(b).length)
      return false;

    for (i = length; i-- !== 0;)
      if (!hasProp.call(b, keys[i])) return false;

    for (i = length; i-- !== 0;) {
      key = keys[i];
      if (!$bw_equal(a[key], b[key])) return false;
    }

    return true;
  }

  return a!==a && b!==b;
}

