export function bw_animate(config: {
  // stepMin?: number,
  stepsMax?: number,
  _equal?: any,
  _steps?: any,
  timeInterval?: number,
  _super: any,
  _master: any,
  _value: any,
  _step_value?: any,
  _data?: any,
  _easing?: any,
  data_key?: string,
  _finish?: any,
}, val?: any, force?: $mol_atom_force) {
  const master = config._master()
  let result = config._super(val, force)
  // if (config.data_key.includes('opacity')) console.log({result, val, force})
  if (val === void 0) {
    const value = config._value(master)
    if (result == null || value == null) {
      result = value
      config._super(result)
    } else {
      let _equal = typeof config._equal == 'function' ? config._equal : $bw_equal
      if (!_equal(result, value)) {
        const _data = typeof config._data == 'function' ? config._data : (() => {
          const key = config.data_key
          if (!key) {
            console.error('bw_animate expects config._data or at least config.data_key', config)
          } else {
            return (val?: {id: any, value: any}) => {
              if (val === void 0) {
                return this[key]
              } else if (val == null) {
                this[key] = null
              } else {
                this[key] = val
              }
            }
          }
        })()
        if (!_data) return
        let data = _data()
        const stopAnimation = () => {
          const data = _data()
          clearInterval(data.id)
          cancelAnimationFrame(data.id)
          // console.log('stopAnimation')
          _data(null)
        }
        if (data && !_equal(data.value, value)) {
          stopAnimation()
        }
        data = _data()
        if (!data) {
          const defaults = {
            steps: 16,
            timeInterval: 16,
          }
          const timeInterval = !config.timeInterval ? defaults.timeInterval : Math.ceil(Math.abs(config.timeInterval))
          const steps =
            typeof config._steps != 'function' && !Number.isFinite(config._steps) ? defaults.steps :
            Math.max(1, Math.round( Math.abs(Number.isFinite(config._steps) ? config._steps : config._steps(result, value))))
          const raf = timeInterval == defaults.timeInterval && window.requestAnimationFrame
          const data = {value} as any
          const nextAnimation = () => {
            if (raf) {
              data.id = raf(stepAnimation)
            }
          }
          const finishAnimation = () => {
            stopAnimation()
            if (typeof config._finish == 'function') {
              config._finish()
            }
          }
          const _easing =
            typeof config._easing == 'function' ?
              config._easing : BwEasing.linear
          const _step_value =
            typeof config._step_value == 'function' ?
              config._step_value :
              (step: number, steps: number, initial: number, target: number) => bw_easing_value(initial, target, step/steps, _easing) //initial + step * (target - initial) / steps
          let step = 0
          const stepAnimation = () => {
            if (step >= steps) {
              finishAnimation()
            } else {
              const value_new = _step_value(++step, steps, result, value)
              config._super(value_new)
              nextAnimation()
            }
          }
          data.id = raf ? raf(stepAnimation) : setInterval(stepAnimation, timeInterval)
          _data(data)
        }
      }
    }
  }
  return result
}

