export type BwAnimatePropertyDescriptor<Value> = TypedPropertyDescriptor< (val? : Value, force? : $mol_atom_force) => Value >
export interface BwAnimateParam<TriggerValue, Value> {
  trigger: TriggerValue,
  start: Value,
  end: Value,
  steps?: number,
  step?: number,
  fn_store?: any,
  step_value?: any,
}

interface BwAnimateData {id: any, value: any, is_not_raf: boolean}

function bw_animate_descr_value<Host, Value, Key>(
  obj: Host,
  name: string,
  fn_initial: any,
  fn_store: any,
  fn_is: any,
  has_key = false,
) {
  // (obj as any)[name + '_store'] = fn_store
  const fn_equal = obj[name + '_equal'] || $bw_equal
  const fn_trigger = obj[name + '_animation_trigger']
  const fn_triggered_value = obj[name + '_animation_triggered_value']
  const fn_steps = obj[name + '_animation_steps']
  const fn_step_value = obj[name + '_animation_step_value']
  const fn_easing = obj[name + '_animation_easing']
  const fn_start = obj[name + '_animation_start']
  const fn_after_step = obj[name + '_animation_after_step']
  const fn_finish = obj[name + '_animation_finish']

  return function $bw_animate_value<Value, Key>(key?: Key) {
    const data_key = '_' + name + '_anim_data' + (has_key ? `_${key}` : '')
    const keyArg = has_key ? [key] : []
    const host: $mol_view  = this
    const trigger = fn_trigger ? fn_trigger.apply(host, [...keyArg]) : fn_initial.apply(this, [...keyArg])
    let result = fn_store.apply(host, [...keyArg])
    if (result === null) {
      result = !fn_trigger ? trigger : fn_initial.apply(this, [...keyArg])
      fn_store.apply(host, [...keyArg, result])
    }

    const value = !fn_triggered_value ? trigger : fn_triggered_value.apply(host, [...keyArg, trigger])
    if (result == null || value == null) {
      result = value
      fn_store.apply(host, [...keyArg, result])
    } else {
      if (!fn_equal.call(host, result, value)) {
        const stopAnimation = () => {
          const data = this[data_key]
          data.is_not_raf ? clearInterval(data.id) : cancelAnimationFrame(data.id)
          this[data_key] = null
        }

        let data = this[data_key]
        if (data && !fn_equal.call(host, data.value, value)) {
          stopAnimation()
          data = null
        }

        if (!data) {
          const defaults = {
            steps: 16,
            timeInterval: 16,
            easing: BwEasing.linear,
          }
          const steps = !fn_steps ? defaults.steps :
            Math.max(1, Math.round(Math.abs(
              fn_steps.apply(host, [...keyArg, {start: result, end: value, trigger}])
            )))
          let step = 0
          const finishAnimation = () => {
            stopAnimation()
            fn_is.apply(host, [...keyArg, false])
            fn_finish && fn_finish.apply(host, [...keyArg, {start: result, end: value, trigger, steps, fn_store}])
          }
          const nextAnimation = () => {
            const data = this[data_key]
            if (!data.is_not_raf) data.id = requestAnimationFrame(stepAnimation)
          }
          const stepAnimation = () => {
            if (step++ >= steps) {
              finishAnimation()
            } else {
              const args = [...keyArg, {step, steps, start: result, end: value, trigger}]
              const value_new = fn_step_value ?
                fn_step_value.apply(host, args) :
                bw_easing_value(result, value, step/steps,
                  fn_easing ? fn_easing.apply(host, args) :
                  defaults.easing
                )
              fn_store.apply(host, [...keyArg, value_new])
              if (typeof fn_after_step == 'function') {
                fn_after_step.apply(host, [...keyArg, {step, steps, start: result, end: value, trigger, step_value: value_new}])
              }
              nextAnimation()
            }
          }

          const timeInterval = defaults.timeInterval //!config.timeInterval ? defaults.timeInterval : Math.ceil(Math.abs(config.timeInterval))
          const raf = timeInterval == defaults.timeInterval && window.requestAnimationFrame
          this[data_key] = {
            value,
            is_not_raf: !raf,
            id: raf ? raf(stepAnimation) : setInterval(stepAnimation, timeInterval),
          }
          // console.log({name})
          fn_is.apply(host, [...keyArg, true])
          // if (name == 'main_params_opacity') {
          //   // console.log({name})
          //   console.log(host, fn_is.apply(host, [...keyArg]), host['main_params_opacity_animation_is']())
          // }
          if (typeof fn_start == 'function') {
            fn_start.apply(host, [...keyArg, {steps, start: result, end: value, trigger}])
          }
        }
      }
    }

    return result
  }
}

export function $bw_animate<TriggerValue, Host, Value>(
  obj : Host,
  name : string ,
  descr : BwAnimatePropertyDescriptor<Value>,
) {
  let fn_initial = descr.value
  descr.value = function( val? : Value , force? : $mol_atom_force ) {
    return ( val !== void 0 ) ? val : null
  }
  $mol_mem<Host, Value>(obj, name, descr)
  const fn_store = descr.value

  const animation_is_name = name + '_animation_is'
  obj[animation_is_name] = function(val?: boolean, force?: $mol_atom_force) {
    return ( val !== void 0 ) ? val : false
  }
  const animation_is_descr = Object.getOwnPropertyDescriptor(obj, animation_is_name)
  $mol_mem<Host, boolean>(obj, animation_is_name, animation_is_descr)
  const fn_is = animation_is_descr.value
  Object.defineProperty(obj, animation_is_name, animation_is_descr)
  // if (animation_is_name == 'main_params_opacity_animation_is') console.log({fn_is}, animation_is_descr)

  descr.value = bw_animate_descr_value<Host, Value, number>(
    obj,
    name,
    fn_initial,
    fn_store,
    fn_is,
  )
  descr.value[ 'mem_value' ] = fn_store
}

export type BwAnimateKeyPropertyDescriptor<Key, Value> = TypedPropertyDescriptor< (key: Key, val? : Value, force? : $mol_atom_force) => Value >

export function $bw_animate_key<TriggerValue, Host, Key, Value>(
  obj : Host,
  name : string ,
  descr : BwAnimateKeyPropertyDescriptor<Key, Value>,
) {
  let fn_initial = descr.value
  descr.value = function( key: Key, val? : Value , force? : $mol_atom_force ) {
    return ( val !== void 0 ) ? val : null
  }
  $mol_mem_key<Host, Key, Value>(obj, name, descr)
  const fn_store = descr.value

  const animation_is_name = name + '_animation_is'
  obj[animation_is_name] = function(key: Key, val?: boolean, force?: $mol_atom_force) {
    return ( val !== void 0 ) ? val : false
  }
  const animation_is_descr = Object.getOwnPropertyDescriptor(obj, animation_is_name)
  $mol_mem_key<Host, Key, boolean>(obj, animation_is_name, animation_is_descr)
  const fn_is = animation_is_descr.value
  Object.defineProperty(obj, animation_is_name, animation_is_descr)

  descr.value = bw_animate_descr_value<Host, Value, Key>(
    obj,
    name,
    fn_initial,
    fn_store,
    fn_is,
    true
  )
  descr.value[ 'mem_value' ] = fn_store
}

