export type BwMeterPropertyDescriptor<Value> = TypedPropertyDescriptor< (val? : Value, force? : $mol_atom_force) => Value >

export function $bw_meter<Host, Value>(
  obj : Host,
  name : string ,
  descr : BwMeterPropertyDescriptor<Value>,
) {
  let fn_source = descr.value
  descr.value = function( val? : Value , force? : $mol_atom_force ) {
    return ( val !== void 0 ) ? val : null
  }
  $mol_mem<Host, Value>(obj, name, descr)
  const fn_store = descr.value
  descr.value = bw_meter_descr_value<Host, Value, number>(
    obj,
    name,
    fn_source,
    fn_store
  )
  descr.value[ 'mem_value' ] = fn_store
}


export type BwMeterKeyPropertyDescriptor<Key, Value> = TypedPropertyDescriptor< (key: Key, val? : Value, force? : $mol_atom_force) => Value >

export function $bw_meter_key<Host, Key, Value>(
  obj : Host,
  name : string ,
  descr : BwMeterKeyPropertyDescriptor<Key, Value>,
) {
  let fn_source = descr.value
  descr.value = function( key: Key, val? : Value , force? : $mol_atom_force ) {
    return ( val !== void 0 ) ? val : null
  }
  $mol_mem_key<Host, Key, Value>(obj, name, descr)
  const fn_store = descr.value
  descr.value = bw_meter_descr_value<Host, Value, Key>(
    obj,
    name,
    fn_source,
    fn_store,
    true
  )
  descr.value[ 'mem_value' ] = fn_store
}

function bw_meter_descr_value<Host, Value, Key>(
  obj: Host,
  name: string,
  fn_source: any,
  fn_store: any,
  has_key = false,
) {
  return function $bw_meter_value<Value, Key>(key?: Key, val? : Value , force? : $mol_atom_force ) {
    const data_key = '_' + name + '_meter_data' + (has_key ? `_${key}` : '')
    const keyArg = has_key ? [key] : []
    const host: $mol_view  = this
    const adjust_value = () => {
      new $mol_defer(() => {
        let result : any
        result = fn_source.apply(host, [...keyArg])
        if (result !== null) {
          const {left, top, width, height, bottom, right} = result
          result = {left, top, width, height, bottom, right}
        }
        fn_store.apply(host, [...keyArg, result])
      })
    }
    if (!this[data_key]) {
      window.addEventListener('resize', adjust_value)
      window.addEventListener('bw_resize', adjust_value)
      this[data_key] = true
    }
    let result = fn_store.apply(host, [...keyArg])
    if (result === null) {
      adjust_value()
    }
    return result
  }
}

