export type BwConditionalKeyPropertyDescriptor<Key, Value> = TypedPropertyDescriptor< (key: Key) => Value >
export type BwConditionalPropertyDescriptor<Value> = TypedPropertyDescriptor< () => Value >

export function $bw_conditional<Host, Value>(
  obj : Host,
  name : string ,
  descr : BwConditionalPropertyDescriptor<Value>,
) {
  descr.value = bw_conditional_descr_value<Host, Value, number>(obj, name, descr)
}

export function $bw_conditional_key<Host, Key, Value>(
  obj : Host,
  name : string ,
  descr : BwConditionalKeyPropertyDescriptor<Key, Value>,
) {
  descr.value = bw_conditional_descr_value<Host, Value, Key>(obj, name, descr, true)
}


function bw_conditional_descr_value<Host, Value, Key>(
  obj : Host,
  name : string ,
  descr : any,
  has_key = false,
) {
  const condition_name = name + '_condition'
  const fn_condition = obj[condition_name]
  if (typeof fn_condition != 'function') {
    const constructorName = obj.constructor.name
    console.error(constructorName + ': @ $bw_conditional' + (has_key ? '_key' : '') + ' ' + name + '() REQUIRES ' + condition_name + '(): boolean {...}')
  }

  const else_name = name + '_else'
  const fn_else = obj[else_name]

  let fn_source = descr.value

  const condition_descr = Object.getOwnPropertyDescriptor(obj, condition_name)
  if (has_key) {
    $mol_mem_key<Host, Key, boolean>(obj, condition_name, condition_descr)
  } else {
    $mol_mem<Host, boolean>(obj, condition_name, condition_descr)
  }
  const fn_condition_atom = condition_descr.value
  Object.defineProperty(obj, condition_name, condition_descr)

  const fn_store = descr.value
  const fn_result = function $bw_conditional_value<Value, Key>(key?: Key) {
    const keyArg = has_key ? [key] : []
    const result =
      fn_condition_atom.apply(this, [...keyArg]) ?
        fn_source.apply(this, [...keyArg]) :
        (!fn_else ? null : fn_else.apply(this, [...keyArg]))
    return result
  }
  let result: any
  if (has_key) {
    result = function( key: Key, val? : Value , force? : $mol_atom_force ) {
      return fn_result.call(this, key)
    }
    $mol_mem_key<Host, Key, Value>(obj, name, descr)
  } else {
    result = function( val? : Value , force? : $mol_atom_force ) {
      return fn_result.call(this)
    }
    $mol_mem<Host, Value>(obj, name, descr)
  }
  return result
}

