export const enum Storage { session, local }

type StoredPropertyDescriptor<Value> = TypedPropertyDescriptor< (next? : Value ) => Value >
var initials : Map<string , any>
function getInitials() {
  if (!initials) {
    initials = new Map<string , any>()
  }
  return initials
}

type BwStore<Value> = (val: Value | null) => any
type BwRestore<Value> = (val_store: any) => Value | null
// type BwEqual<Value> = (a: Value, b: Value) => boolean

export function $bw_session<Host, Value>(
  obj : Host,
  name : string ,
  descr : StoredPropertyDescriptor<Value>,
) {
  const result = $bw_store('session')(obj, name, descr)
  // console.log(result)
  return result
}

export function $bw_local<Host, Value>(
  obj : Host,
  name : string ,
  descr : StoredPropertyDescriptor<Value>,
) {
  return $bw_store('local')(obj, name, descr)
}

export function $bw_store<Value>(
  storage_name: 'session' | 'local',
  need_transform: boolean | null = null
) {
  const storage = storage_name == 'session' ? Storage.session : Storage.local

  return function bw_storable<Host, Value>(
    obj : Host,
    name : string ,
    descr : StoredPropertyDescriptor<Value>,
    ) {
    const value = descr.value
    let fn_store : BwStore<Value>
    let fn_restore : BwRestore<Value>
    let fn_equal : BwEqual<Value> = obj[name + '_equal']
    if (typeof fn_equal != 'function') fn_equal = $bw_equal
    if (need_transform !== false) {
      fn_store = obj[name + '_store']
      fn_restore = obj[name + '_restore']
      if (need_transform) {
        if (typeof fn_store != 'function') {
          console.error(`@ $bw_${storage_name} ${ obj }.${name} REQUIRES ${ obj }.${name}_store(val: Value): any `)
        }
        if (typeof fn_restore != 'function') {
          console.error(`@ $bw_${storage_name} ${ obj }.${name} REQUIRES ${ obj }.${name}_restore(val_store: any): Value`)
        }
      }
    }
    // let mol_defer: $mol_defer
    let timer : any
    descr.value = function $bw_storage_value(
      next? : Value,
      need_store?: boolean
    ) {
      // console.log({name, next})
      const host: $mol_view  = this
      const id = `${ host }.${ name }()`
      const state = (val?: any) => {
        const val_store = val === void 0 ? void 0 : fn_store ? fn_store.call(obj, val) : val as any
        // if (val !== void 0) {
        //   if (id.includes('col_defs')) console.error({val, val_store})
        // }
        const result_store = storage === Storage.session ?
          this.$.$mol_state_session.value(id, val_store) :
          this.$.$mol_state_local.value(id, val_store)
        let result : Value
        if (val === void 0) {
          result = fn_restore ? fn_restore.call(obj, result_store) : result_store as Value
        }
        return result
      }
      let result : Value
      let initial : Value
      const initials = getInitials()
      if (next !== void 0) {
        if (!initials.has(id)) {
          initial = value.call(host)
          initials.set(id, initial)
        } else {
          initial = initials.get(id)
        }
        result = value.apply(host, arguments)
        if (result === void 0) result = next

        const stored = initial !== void 0 && fn_equal.call(obj, result, initial) ? null : result
        if (stored === null) {
          state(stored)
        } else {
          if (timer !== void 0) clearTimeout(timer)
          timer = setTimeout(() => {
            state(stored)
            timer = void 0
          }, 100)
        }
      } else {
        result = value.apply(host, arguments)
        // if (name == 'orders') console.log({result, initials})
        let is_initial = false
        if (!initials.has(id)) {
          initial = result
          initials.set(id, initial)
          is_initial = true
        } else {
          initial = initials.get(id)
          is_initial = fn_equal.call(obj, result, initial)
        }
        if (is_initial) {
          const stored = state()
          if (stored == null) {
            result = initial
          } else {
            result = value.call(host, stored)
            if (result === void 0) result = stored
          }
        }
        // if (name == 'orders') console.log({result})
      }
      // if (name.endsWith('is_narrow')) console.log({next, result})
      return result
    }

    descr.value[ 'value' ] = value
    return descr
  }

}

