export function $bw_false_on_outside_click(
  obj? : $mol_view ,
  name? : string ,
  descr? : TypedPropertyDescriptor< ( next? : boolean , force? : $mol_atom_force )=> boolean >
) {
  const value = descr.value
  descr.value = function $bw_false_on_outside_click_impl( next? : boolean , force? : $mol_atom_force ) {
    const host: $mol_view  = this
    if (next !== void 0) {
      const outsideClickListener = (event : Event) => {
        // https://stackoverflow.com/questions/152975/how-do-i-detect-a-click-outside-an-element/3028037#3028037
        const target = event.target
        const selector = '#' + host.dom_id().replace(/([^\w\d_-])/g, '\\$1');
        const closest = (target as any).closest(selector)
        if (closest === null) {
          document.removeEventListener('click', outsideClickListener)
          value.call(host, false)
        }
      }
      const methodName = next ? 'addEventListener' : 'removeEventListener'
      document[methodName]('click', outsideClickListener)
    }
    return value.apply( host , arguments )
  }
  descr.value[ 'value' ] = value
}

// ==========================================================================

export function plural_word(count : number, word1 : string, word2_4 : string, word5more? : string) {
  if (word5more === undefined) { word5more = word2_4 }
  let result = word5more
  const decimal = Math.floor(count / 10) % 10
  if (decimal != 1) {
    const unit = count % 10
    if (unit == 1) {
      result = word1
    } else if ( unit >= 2 && unit <= 4 ) {
      result = word2_4
    }
  }
  return result
}

// ==========================================================================

export function smoothScroll(dom_node : Element, scrollTo: number, after?: any) {
  // console.log('smoothScroll')
  scrollTo = Math.min(Math.max(0, scrollTo), dom_node.scrollHeight - dom_node.clientHeight)
  let lastScrollTop = dom_node.scrollTop
  const delta = scrollTo - lastScrollTop
  // console.log({scrollTo, delta})
  if (delta != 0) {
    let lastScrollDelta = 0
    let isScrolling : any
    const scrollListener = () => {
      if (event.target === dom_node) {
        const scrollTop = dom_node.scrollTop
        lastScrollDelta = scrollTop - lastScrollTop
        lastScrollTop = scrollTop

        clearTimeout(isScrolling)
        if (scrollTop == scrollTo) {
          if ((dom_node as any).stopSmoothScroll) {
            (dom_node as any).stopSmoothScroll()
            if (after !== void 0) {
              after()
            }
          } else {
            console.error('BUG: unreachable')
            dom_node.removeEventListener('scroll', scrollListener)
          }
        } else if (
          Math.sign(lastScrollDelta) != Math.sign(delta) ||
          Math.sign(delta) > 0 && scrollTop > scrollTo ||
          Math.sign(delta) < 0 && scrollTop < scrollTo
        ) {
          if ((dom_node as any).stopSmoothScroll) {
            (dom_node as any).stopSmoothScroll()
          } else {
            console.error('BUG: unreachable')
            dom_node.removeEventListener('scroll', scrollListener)
          }
        } else {
          isScrolling = setTimeout(function() {
            dom_node.scrollTo({
              top: scrollTo,
              behavior: 'smooth',
            })
          }, 30)
        }
      }
    }
    dom_node.addEventListener('scroll', scrollListener)
    if ((dom_node as any).stopSmoothScroll) {
      (dom_node as any).stopSmoothScroll()
    }
    (dom_node as any).stopSmoothScroll = () => {
      dom_node.removeEventListener('scroll', scrollListener)
      delete (dom_node as any).stopSmoothScroll
    }
    dom_node.scrollTo({
      top: scrollTo,
      behavior: 'smooth',
    })
  } else if (after !== void 0) {
    after()
  }
}

// ==========================================================================

export function adjustOffsetParentScrollTop(
  obj: $mol_view,
  opt: {
    after?: any,
    margin? : { top?: number, bottom?: number } | number,
  }
 ) {
  const node = obj.dom_node() as HTMLElement
  const offsetParent = node.offsetParent
  const offsetParentClientHeight = node.offsetParent.clientHeight
  const margin = opt == void 0 ? void 0 : opt.margin
  const after = opt === void 0 ? void 0 : opt.after
  const topMargin = margin == void 0 ? 0 : typeof margin === 'number' ? margin : (margin.top || 0);
  const bottomMargin = margin == void 0 ? 0 : typeof margin === 'number' ? margin : (margin.bottom || 0);
  const offsetTop = node.offsetTop - topMargin
  const offsetBottom = offsetTop + node.offsetHeight + topMargin + bottomMargin
  const scrollTop = offsetParent.scrollTop
  const scrollBottom = scrollTop + offsetParent.clientHeight
  if (offsetTop < scrollTop) {
    smoothScroll(offsetParent, offsetTop, after)
  } else if (offsetTop > scrollTop && offsetBottom > scrollBottom) {
    const deltaTop = offsetTop - scrollTop
    const deltaBottom = offsetBottom - scrollBottom
    const scrollTo = scrollTop + Math.min(deltaTop, deltaBottom)
    smoothScroll(offsetParent, scrollTo, after)
  }
}

// ==========================================================================

export function actualOffset(fromEl : HTMLElement | $mol_view, toEl : HTMLElement | $mol_view, silent = false) {
  if (fromEl instanceof $mol_view) fromEl = fromEl.dom_node() as HTMLElement
  if (toEl instanceof $mol_view) toEl = toEl.dom_node() as HTMLElement
  let result = {x: 0, y: 0}
  while (true) {
    result.x += fromEl.offsetLeft
    result.y += fromEl.offsetTop
    const offsetParent = fromEl.offsetParent
    if (!offsetParent) {
      if (!silent) {
        console.error({msg: 'fromEl is not descendant of toEl', fromEl, toEl})
        console.trace()
      }
      return null
    } else if (offsetParent === toEl) {
      return result
    } else {
      fromEl = offsetParent as HTMLElement
      result.x += fromEl.offsetWidth - fromEl.clientWidth
      result.y += fromEl.offsetHeight - fromEl.clientHeight
    }
  }
}

// ==========================================================================

export function do_autofocus(obj : $mol_view) {
  new $mol_defer( () => {
    let autofocusElement = obj.dom_node().querySelector('[autofocus]') as HTMLElement
    if (autofocusElement) {
      autofocusElement.focus()
    } else {
      let autofocusElement = obj.dom_node().querySelector('[bw_autofocus]') as HTMLElement
      if (autofocusElement) {
        autofocusElement.focus()
      }
    }
  })
}

// ==========================================================================

export function $bw_is_self_or_descendant(testNode: Node, targetNode: Node) {
  let result = false
  while (testNode) {
    result = testNode == targetNode
    if (result) break
    testNode = testNode.parentNode
  }
  return result
}

