interface EventSeqCode {ctrlKey: boolean, metaKey: boolean, shiftKey: boolean, altKey: boolean, code: string}
interface EventSeqNumber { value: number }
export interface KeySeqCode {
  code: string,
  ctrlKey?: boolean | null,
  metaKey?: boolean | null,
  shiftKey?: boolean | null,
  altKey?: boolean | null,
}
export interface KeySeqNumber {default?: number}
export type VimCmdFunc = (opt?: VimCmdOpt, cmd?: VimCmd) => (void | {mode: VimMode})
export interface EventConformResult { code: string, ctrlKey?: boolean, metaKey?: boolean, shiftKey?: boolean, altKey?: boolean }
export interface VimCmdOpt {
  qt?: number,
  conformResult?: EventConformResult | EventConformResult[],
}

export class VimDefCmd {
  vim_cmd = [] as VimCmdFunc[]
  constructor(...items: VimDefCmdItem[]) {
    items.forEach(item => {
      this.set(item.cmd, item.fn)
    })
  }
  set(cmd: VimCmd | VimCmd[], fn: VimCmdFunc) {
    let val = (typeof cmd == 'object' ? cmd : [cmd]) as VimCmd[]
    const count = val.length
    for (let j = 0; j < count; j++) {
      if (this.vim_cmd[val[j]]) {
        console.error(`${fn} and ${ this.vim_cmd[val[j]] } are assigned to the same cmd ${ val[j] }`)
        console.trace()
        return
      }
      this.vim_cmd[val[j]] = fn
    }
  }
}

export class VimDefCmdItem {
  cmd: VimCmd | VimCmd[]
  fn: VimCmdFunc
  constructor (cmd: VimCmd | VimCmd[], fn: VimCmdFunc) {
    this.cmd = cmd
    this.fn = fn
  }
}

export function $bw_vim_event_conform(tstCode: EventSeqCode, etaCode: KeySeqCode) : boolean | EventConformResult {
  if (tstCode.code !== etaCode.code) return false
  let result : boolean | EventConformResult = true
  const keys = ['ctrlKey', 'shiftKey', 'altKey', 'metaKey']
  const len = keys.length
  for (let i = 0; i < len; i++) {
    const key = keys[i]
    if (etaCode[key] === null) {
      if (typeof result === 'boolean') {
        result = <EventConformResult>{ code: etaCode.code }
      }
      result[key] = tstCode[key]
    } else if (!!etaCode[key] !== !!tstCode[key]) {
      return false
    }
  }
  return result
}

export function $bw_vim_event_upper(obj: $mol_view) {
  let target: $mol_view
  let host: $mol_view = obj
  while (host = host['object_host()']) {
    if (typeof (host as any).event_keypress != 'function') {
      host = host['object_host()']
    } else {
      target = host
      break
    }
  }
  if (target) {
    (target as any).event_keypress(event)
  }
}

export const enum VimCmd {
  moveCursorUp,
  moveCursorDown,
  moveCursorHalfPageUp,
  moveCursorHalfPageDown,
  moveCursorPageUp,
  moveCursorPageDown,

  setCursorToFirstLine,
  setCursorToLastLine,

  setCursorToTopOfView,
  setCursorToBottomOfView,
  setCursorToMiddleOfView,

  scrollCursorUp,
  scrollCursorDown,

  scrollCursorToTopOfView,
  scrollCursorToBottomOfView,
  scrollCursorToMiddleOfView,

  insertLineAboveCursor,
  insertLineBelowCursor,

  // insertTextBeforeCursor,
  // insertTextAfterCursor,

  deleteLines,
  backspace,

  undo,
  redo,

  enter,
  escape,
  space,
  visualLine,
  selectAll,
  invertSelection,
  pasteAfter,
  pasteBefore,
  replace,
}

