namespace $ { export class $bw_app_way_selector extends $mol_view {

	/// sub / <= Bar
	sub() {
		return [].concat( this.Bar() )
	}

	/// Bar $mol_bar sub <= ways
	@ $mol_mem
	Bar() {
		return (( obj )=>{
			obj.sub = () => this.ways()
			return obj
		})( new this.$.$mol_bar )
	}

	/// ways /
	ways() {
		return [] as any[]
	}

	/// Link!way $bw_link
	/// 	title <= link_title!way
	/// 	arg <= link_arg!way
	/// 	event_click?event <=> link_click!way?event
	@ $mol_mem_key
	Link( way : any ) {
		return (( obj )=>{
			obj.title = () => this.link_title(way)
			obj.arg = () => this.link_arg(way)
			obj.event_click = ( event? : any ) => this.link_click(way , event )
			return obj
		})( new this.$.$bw_link )
	}

	/// link_title!way \
	link_title( way : any ) {
		return ""
	}

	/// link_arg!way *
	link_arg( way : any ) {
		return ({
		})
	}

	/// link_click!way?event null
	@ $mol_mem_key
	link_click( way : any , event? : any , force? : $mol_atom_force ) {
		return ( event !== void 0 ) ? event : null as any
	}

} }

namespace $ { export class $bw_app_old_look extends $mol_view {

	/// sub /
	/// 	<= LookSelector
	/// 	<= Label
	sub() {
		return [].concat( this.LookSelector() , this.Label() )
	}

	/// LookSelector $mol_link
	/// 	event_click?event <=> link_click?event
	/// 	current false
	/// 	title \Новый дизайн
	@ $mol_mem
	LookSelector() {
		return (( obj )=>{
			obj.event_click = ( event? : any ) => this.link_click( event )
			obj.current = () => false
			obj.title = () => "Новый дизайн"
			return obj
		})( new this.$.$mol_link )
	}

	/// link_click?event null
	@ $mol_mem
	link_click( event? : any , force? : $mol_atom_force ) {
		return ( event !== void 0 ) ? event : null as any
	}

	/// Label $mol_view sub / \Старый дизайн
	@ $mol_mem
	Label() {
		return (( obj )=>{
			obj.sub = () => [].concat( "Старый дизайн" )
			return obj
		})( new this.$.$mol_view )
	}

} }

