namespace $ { export class $bw_modal extends $mol_view {

	/// content /
	content() {
		return [] as any[]
	}

	/// shown?val false
	@ $mol_mem
	shown( val? : any , force? : $mol_atom_force ) {
		return ( val !== void 0 ) ? val : false
	}

	/// sub / <= Inner
	sub() {
		return [].concat( this.Inner() )
	}

	/// Inner $mol_view sub /
	/// 	<= Overlay
	/// 	<= Form
	@ $mol_mem
	Inner() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.Overlay() , this.Form() )
			return obj
		})( new this.$.$mol_view )
	}

	/// Overlay $mol_view
	@ $mol_mem
	Overlay() {
		return (( obj )=>{
			return obj
		})( new this.$.$mol_view )
	}

	/// Form $mol_view
	/// 	dom_name \form
	/// 	attr * bw_form_shown <= bw_form_shown
	/// 	sub <= content
	@ $mol_mem
	Form() {
		return (( obj )=>{
			obj.dom_name = () => "form"
			obj.attr = () => ({
			"bw_form_shown" :  this.bw_form_shown() ,
		})
			obj.sub = () => this.content()
			return obj
		})( new this.$.$mol_view )
	}

	/// bw_form_shown <= shown
	bw_form_shown() {
		return this.shown()
	}

} }

