namespace $ { export class $bw_vim extends $mol_plugin {

	/// event *
	/// 	keypress?event <=> event_keypress?event
	/// 	keydown?event <=> event_keydown?event
	event() {
		return ({
			"keypress" :  ( event? : any )=>  this.event_keypress( event ) ,
			"keydown" :  ( event? : any )=>  this.event_keydown( event ) ,
		})
	}

	/// event_keypress?event null
	@ $mol_mem
	event_keypress( event? : any , force? : $mol_atom_force ) {
		return ( event !== void 0 ) ? event : null as any
	}

	/// event_keydown?event null
	@ $mol_mem
	event_keydown( event? : any , force? : $mol_atom_force ) {
		return ( event !== void 0 ) ? event : null as any
	}

} }

