namespace $.$$ {
// ============================================================================
// ============================================================================

export class $bw_block extends $.$bw_block {
  @ $mol_mem
  focused( next?: boolean ) {
    const result = super.focused(next)
    if (result) {
      // console.trace()
      new $mol_defer(() =>
        this.selected_block(this.block_name())
      )
    }
    // console.log({result, next, '': this.block_name() })
    return result
  }

  @ $mol_mem
  is_selected() {
    const result = this.selected_block() === this.block_name()
    return result
  }

  event_click( event? : any , force? : $mol_atom_force ) {
    // console.log({event})
    this.selected_block(this.block_name())
    return event
  }
}

// ============================================================================
// ============================================================================
}