namespace $ { export class $bw_field extends $mol_view {

	/// label \
	label() {
		return ""
	}

	/// Control null
	Control() {
		return null as any
	}

	/// sub /
	/// 	<= Label
	/// 	<= Control
	sub() {
		return [].concat( this.Label() , this.Control() )
	}

	/// Label $bw_label text <= label
	@ $mol_mem
	Label() {
		return (( obj )=>{
			obj.text = () => this.label()
			return obj
		})( new this.$.$bw_label )
	}

} }

