namespace $ { export class $bw_button_major extends $mol_button {

	/// title \
	title() {
		return ""
	}

	/// Icon null
	Icon() {
		return null as any
	}

	/// tabindex 1
	tabindex() {
		return 1
	}

	/// attr *
	/// 	^
	/// 	tabindex <= tabindex
	attr() {
		return ({
			...super.attr() ,
			"tabindex" :  this.tabindex() ,
		})
	}

	/// sub / <= Image
	sub() {
		return [].concat( this.Image() )
	}

	/// Image $mol_view sub /
	/// 	<= Icon
	/// 	<= Caption
	@ $mol_mem
	Image() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.Icon() , this.Caption() )
			return obj
		})( new this.$.$mol_view )
	}

	/// Caption $mol_view sub / <= title
	@ $mol_mem
	Caption() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.title() )
			return obj
		})( new this.$.$mol_view )
	}

} }

