namespace $ { export class $bw_input_checkable extends $mol_view {

	/// value?val \
	@ $mol_mem
	value( val? : any , force? : $mol_atom_force ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// autofocus false
	autofocus() {
		return false
	}

	/// readonly false
	readonly() {
		return false
	}

	/// autocomplete \
	autocomplete() {
		return ""
	}

	/// hint \
	hint() {
		return ""
	}

	/// bw_dropped_down false
	bw_dropped_down() {
		return false
	}

	/// bw_grid_column_filterable true
	bw_grid_column_filterable() {
		return true
	}

	/// input_enabled true
	input_enabled() {
		return true
	}

	/// checked?val false
	@ $mol_mem
	checked( val? : any , force? : $mol_atom_force ) {
		return ( val !== void 0 ) ? val : false
	}

	/// check_enabled true
	check_enabled() {
		return true
	}

	/// debounce 200
	debounce() {
		return 200
	}

	/// minimal_height 32
	minimal_height() {
		return 32
	}

	/// sub /
	/// 	<= Input
	/// 	<= Check
	sub() {
		return [].concat( this.Input() , this.Check() )
	}

	/// Input $bw_input
	/// 	value?val <=> value?val
	/// 	autofocus <= autofocus
	/// 	readonly <= readonly
	/// 	autocomplete <= autocomplete
	/// 	bw_dropped_down <= bw_dropped_down
	/// 	enabled <= input_enabled
	/// 	debounce <= debounce
	/// 	minimal_height <= minimal_height
	/// 	hint <= hint
	@ $mol_mem
	Input() {
		return (( obj )=>{
			obj.value = ( val? : any ) => this.value( val )
			obj.autofocus = () => this.autofocus()
			obj.readonly = () => this.readonly()
			obj.autocomplete = () => this.autocomplete()
			obj.bw_dropped_down = () => this.bw_dropped_down()
			obj.enabled = () => this.input_enabled()
			obj.debounce = () => this.debounce()
			obj.minimal_height = () => this.minimal_height()
			obj.hint = () => this.hint()
			return obj
		})( new this.$.$bw_input )
	}

	/// Check $mol_check
	/// 	checked?val <=> checked?val
	/// 	enabled <= check_enabled
	/// 	sub / <= Icon
	@ $mol_mem
	Check() {
		return (( obj )=>{
			obj.checked = ( val? : any ) => this.checked( val )
			obj.enabled = () => this.check_enabled()
			obj.sub = () => [].concat( this.Icon() )
			return obj
		})( new this.$.$mol_check )
	}

	/// Icon null
	Icon() {
		return null as any
	}

} }

