namespace $ { export class $bw_workspace extends $mol_view {

	/// sub / <= Content
	sub() {
		return [].concat( this.Content() )
	}

	/// Content null
	Content() {
		return null as any
	}

	/// Settings $bw_settings
	@ $mol_mem
	Settings() {
		return (( obj )=>{
			return obj
		})( new this.$.$bw_settings )
	}

	/// Search $bw_search
	@ $mol_mem
	Search() {
		return (( obj )=>{
			return obj
		})( new this.$.$bw_search )
	}

} }

