namespace $.$$ {

  let bw_login_data : $bw_login_data

  export class $bw_login_data extends $mol_object {
    pass_opened(val? : any) {
      return this.$.$mol_state_session.value( '$bw_signin_form_pass_opened', val) || false
    }
    pass_type() {
      return this.pass_opened() ? "text" : "password"
    }
  }

  export class $bw_login extends $.$bw_login {
    constructor () {
      super()
      bw_login_data = new $bw_login_data()
    }
    title() {
      return this.Form().title()
    }
  }

  export class $bw_login_form extends $.$bw_login_form {
    title() {
      let value = this.Deck().Switch().value()
      return {
        '0': { title: 'Вход в WinNER7' },
        '1': { title: 'Регистрация в WinNER7' },
      }[value].title
    }
  }

  export class $bw_pass_input_icon extends $.$bw_pass_input_icon {
    Icon() {
      return this.checked() ? this.OpenIcon() : this.CloseIcon()
    }
  }

  export class $bw_pass_input extends $.$bw_pass_input {
    @ $mol_mem
    pass_input_icon_click( val? : any , force? : $mol_atom_force ) {
      if (val) {
        this.Icon().checked( !this.Icon().checked() )
        new $mol_defer( ()=> this.Input().focused(true) )
      }
      return ( val !== void 0 ) ? val : null as any
    }
    pass_opened( val? : any , force? : $mol_atom_force ) {
      return bw_login_data.pass_opened(val)
    }
    attr() {
      return {
        focused: this.Input().focused()
      }
    }
  }

  export class $bw_signin_form_pass_input extends $.$bw_signin_form_pass_input {
    @ $mol_mem
    pass_type() {
      return bw_login_data.pass_type()
    }
  }
}
