export class $bw_input extends $.$bw_input {
  vim_cmd() {
    return new VimDefCmd(
      new VimDefCmdItem(VimCmd.backspace, (opt: VimCmdOpt, cmd: VimCmd) => {
        console.log(cmd)
      })
    )
  }
  vim_modes() {
    return VimMode.insert
  }
  vim_process_keypress(event: KeyboardEvent) {
    const result = event.code != 'Enter'
    return result
  }
}

