  @ $bw_session
  row_footer_heights(val?: Map<number, BwGrid2RowFooterHeight>, force?: $mol_atom_force) {
    let result = super.row_footer_heights(val, force)
    if (!result) {
      result = new Map<number, BwGrid2RowFooterHeight>()
    }
    return result
  }
  row_footer_heights_store(val: Map<string, BwGrid2RowFooterHeight>|null) {
    return !val ? null : [...val.entries()]
  }
  row_footer_heights_restore(val_store: any) {
    const result = !Array.isArray(val_store) ? null : new Map<number, BwGrid2RowFooterHeight>(
      val_store
        .filter((item: any) =>
          Array.isArray(item) &&
          typeof item[0] == 'number' &&
          (item[1] !== null && typeof item[1] == 'object') &&
          Number.isInteger(item[1].footer) && Number.isInteger(item[1].note) &&
          true
        ).map((item: [number, BwGrid2RowFooterHeight]) => {
          const note = Math.max(this.row_footer_item_height_min(), item[1].note)
          const footer = Math.max(this.row_footer_item_height_min() + note, item[1].footer)
          return [ item[0], <BwGrid2RowFooterHeight>{ note, footer } ] as [number, BwGrid2RowFooterHeight]
        })
    )
    return result
  }
  @ $mol_mem
  is_bog(val?: boolean, force?: $mol_atom_force) {
    const result = this.rows_count() < 2 || super.is_bog(val, force)
    return result
  }
  @ $mol_mem
  is_eog(val?: boolean, force?: $mol_atom_force) {
    const result = this.rows_count() < 2 || super.is_eog(val, force)
    return result
  }
  @ $mol_mem
  row_space_bottom_max() {
    let result = this.body_height()
    for (let idx = this.rows_count() - 1; idx >= 0; idx--) {
      const height = this.row_height_of_idx(idx)
      if (result - height <= 0) break
      result -= height
    }
    return result
  }
  @ $mol_mem
  Body() {
    const result = super.Body()
    const raf = window.requestAnimationFrame
    let raf_id : any
    let scrollAccu = 0
    let lastDelta : number
    const isPassive = false
    // const isPassive = true
    const stepAnimation = () => {
      this.scroll_y(scrollAccu)
      raf_id = void 0
      scrollAccu = 0
    }
    const mousewheelListener = (event: MouseWheelEvent) => {
      // console.log(event)
      const deltaY = event.deltaY
      if (!deltaY) return
      if (Math.sign(lastDelta) != Math.sign(deltaY)) {
        // this.scroll_y_delta(deltaY)
        scrollAccu = deltaY
      } else {
        // this.scroll_y_delta(this.scroll_y_delta() + deltaY)
        scrollAccu += deltaY
      }
      lastDelta = deltaY
      // const scroll_y_delta = this.scroll_y_delta()
      if (scrollAccu < 0 && !this.is_bog() || scrollAccu > 0 && !this.is_eog()) {
        if (!isPassive) event.preventDefault()
        if (raf_id == void 0) {
          raf_id = raf ? raf(stepAnimation) : setTimeout(stepAnimation, 16)
        }
      }
    }
    // result.dom_node().addEventListener('mousewheel', mousewheelListener, {passive: isPassive})
    result.dom_node().addEventListener('wheel', mousewheelListener, {passive: isPassive})
    new $mol_defer(() => {
      this.rows_count()
      // new $mol_defer(() => this.row_defs() )
    })
    return result
  }
  // body_mousewheel(event?: MouseWheelEvent) {
