export class $bw_settings extends $.$bw_settings {
  private _lastScrollTop: number = 0
  private _scrollingTimer: any
  _content_scroll(dom_node: HTMLElement) {
    const scrollTop = dom_node.scrollTop
    const scrollDelta = scrollTop - this._lastScrollTop
    this._lastScrollTop = scrollTop
    if (!(dom_node as any).stopSmoothScroll) {
      clearTimeout(this._scrollingTimer)
      this._scrollingTimer = setTimeout(() => {
        const clientHeight = dom_node.clientHeight
        const scrollBottom = scrollTop + clientHeight
        let block : HTMLElement
        if (scrollDelta > 0) {
          const items : any = [...dom_node.querySelectorAll('[bw_block]')]
            .map((node : Element) => node as HTMLElement)
            .map((node : HTMLElement) => {
              const caption = node.querySelector('[bw_block_caption]') as HTMLElement
              return {
                node,
                offsetTop: actualOffset(node, dom_node).y,
                offsetBottom: actualOffset(caption, dom_node).y + caption.offsetHeight,
              }
            })
            .filter((value : {offsetBottom: number, offsetTop: number}) =>
              value.offsetBottom > scrollTop && value.offsetTop < scrollBottom
            )
            .sort(
              (
                a: {node : HTMLElement, offsetTop: number},
                b: {node : HTMLElement, offsetTop: number},
              ) => (a.offsetTop - b.offsetTop) || (a.node.offsetLeft - b.node.offsetLeft)
            )
          if (items.length && items[0].offsetTop >= scrollTop) {
            block = items[0].node
          }
        } else if (scrollDelta < 0) {
          const items : any = [...dom_node.querySelectorAll('[bw_block]')]
            .map((node : Element) => node as HTMLElement)
            .map((node : HTMLElement) => {

              const offsetTop = actualOffset(node, dom_node).y
              return {
                node,
                offsetTop: offsetTop,
                offsetBottom: offsetTop + node.offsetHeight,
              }

            })
            .filter((value : {offsetBottom: number, offsetTop: number}) =>
              value.offsetBottom > scrollTop && value.offsetBottom < scrollBottom
            )
            .sort(
              (
                a: {node : HTMLElement, offsetTop: number},
                b: {node : HTMLElement, offsetTop: number},
              ) => (b.offsetTop - a.offsetTop) || (a.node.offsetLeft - b.node.offsetLeft)
            )
          if (items.length && items[0].offsetTop <= scrollTop) {
            block = items[0].node
          }
        }

        if (block) {
          new $mol_defer(() => {
            const block_name = block.getAttribute('bw_block_name')
            this.selected_block(block_name)
          })
        }
      }, 30)
    }
  }

  content_scroll( event? : any ) {
    const dom_node = event.target as HTMLElement
    if (dom_node == this.Content().dom_node()) {
    // console.log(event)
      new $mol_defer(() => this._content_scroll(dom_node))
    }
  }

  menu_option_enabled(id : string) {
    return id != this.selected_block()
  }
  menu_options() {
    const blocks = this.blocks()
    const result = Object.keys(blocks)
      .reduce(
        (accu: {}, key : string) => ({...accu, [key]: blocks[key].switch_title}),
        {}
      )
    return result
  }

  @ $bw_session
  selected_block( val? : any , force? : $mol_atom_force) {
    const result = super.selected_block()
    if (val === void 0) {
      return result
    } else {
      if (result !== val) {
        super.selected_block(val, force)
        new $mol_defer(() => {
          const blocks = this.blocks()
          const block = blocks[val].block
            const activeElement = document.activeElement as HTMLElement
            if (activeElement) activeElement.blur()
              // console.log('THERE')
            adjustOffsetParentScrollTop(block, {
              margin: 16,
              after: () => do_autofocus(block),
            })
        })
      }
      return val
    }
  }
  // selected_block_store(val: string) {
  //   return val
  // }
  // selected_block_restore(val_store: string) {
  //   return val_store
  // }
}

