export class $bw_settings_blacklist_block extends $.$bw_settings_blacklist_block {

  records_modify( val? : BwRecordsModify ) {
    if (val !== void 0) {
      if (val && val.state === BwGridModifyState.opened) {
        if (val.kind == BwGridModifyKind.insert) {
          this.phone_number('')
          this.phone_desc('')
        } else {
          let phone = ''
          let description = ''
          const records = val.records
          if (records) {
            const rec_id = records.keys().next().value
            if (rec_id) {
              const record = records.get(rec_id)
              phone = record.phone
              description = record.description
            }
          }
          this.phone_number(phone)
          this.phone_desc(description)
        }
      }
    }
    return super.records_modify(val)
  }

  grid_height() {
    const result = super.grid_height()
    if (result) {
      new $mol_defer(() =>
        adjustOffsetParentScrollTop(this, {margin: 16})
      )
    }
    return result
  }
  AddModal() {
    const result = !this.add_form_shown() ? null : super.AddModal()
    return result
  }
  add_click( event? : any , force? : $mol_atom_force ) {
    if (event !== void 0) {
      this.Grid().new_record()
    }
    return super.add_click(event, force)
  }

  add_form_shown( val? : any ) {
    const records_modify = this.records_modify()
    return records_modify && records_modify.state == BwGridModifyState.opened
  }

  del_click( val? : any , force? : $mol_atom_force ) {
    this.Grid().cmd_del()
  }

  cancel_records() {
    this.records_modify(null)
  }

  commit_records() {
    const records_modify = this.records_modify()
    if (records_modify) {
      const data = this.data()
      let records = new Map<string, any>()
      if (records_modify.kind == BwGridModifyKind.edit) {
        if (records_modify.records) {
          const rec_id = records_modify.records.keys().next().value
          // console.log(records_modify, rec_id)
          if (rec_id) {
            records.set(rec_id, {
              phone: this.phone_number(),
              description: this.phone_desc(),
              author: 'Новосельцев Станислав Игоревич',
            })
          }
        }
      } else {
        records.set(data.nextId, {
          phone: this.phone_number(),
          description: this.phone_desc(),
          author: 'Новосельцев Станислав Игоревич',
        })
      }
      const records_new = { ...records_modify, state: BwGridModifyState.commited, records }
      // console.log(records_new)
      this.records_modify(records_new)
    }
  }

  @ $bw_store('local', true)
  data( val? : BlacklistData , force? : $mol_atom_force ) {
    let result = super.data(val, force) || <BlacklistData>{nextId : 0, records: new Map<string, any>()}
    return result
  }
  data_store(val: BlacklistData) {
    const result = { nextId: val.nextId, records: [...val.records.entries()] }
    return result
  }
  data_restore(val_store: any) {
    let result : BlacklistData | null = null
    if (val_store && typeof val_store == 'object') {
      result = { nextId: val_store.nextId, records: new Map(val_store.records) }
    }
    return result
  }
  data_equal(a: BlacklistData, b: BlacklistData) : boolean {
    if (a === b) return true
    if (a.nextId !== b.nextId) return false
    return $bw_equal([...a.records.entries()], [...b.records.entries()])
  }

  records( records? : BwGridRecords) {
    if (records !== void 0) {
      const old_data = this.data()
      let nextId = old_data.nextId
      records.forEach((rec: any, rec_id : string) => {
        if (nextId <= +rec_id) nextId = +rec_id + 1
      })
      const new_data = <BlacklistData>{ nextId, records }
      this.data(new_data)
    }
    const result = this.data().records || new Map<string, any>()
    return result
  }

  filter() {
    let result = <BwGridFilter>{}
    if (this.filter_phone()) {
      result['phone'] = <BwGridColFilter>{
        type: BwGridColFilterType.contains,
        contains: this.filter_phone(),
      }
    }
    if (this.filter_description()) {
      result['description'] = <BwGridColFilter>{
        type: BwGridColFilterType.contains,
        contains: this.filter_description(),
      }
    }
    result = Object.keys(result).length ? result : null
    const fixed_height = !result ? null : this.Grid().current_grid_height
    this.Grid().fixed_height(fixed_height)

    return result
  }
  add_modal_title() {
    const records_modify = this.records_modify()
    const result = records_modify && records_modify.state == BwGridModifyKind.insert ? 'Добавление телефона' : 'Редактирование телефона'
    return result
  }
}
interface BlacklistData {
  nextId: number,
  records: BwGridRecords,
}

