export class $bw_settings_blacklist_block_add_modal extends $.$bw_settings_blacklist_block_add_modal {
  event_click( event? : any) {
    this['object_host()'].commit_records()
  }
  phone_autofocus() {
    return this.is_new_record_to_edit()
  }
  description_autofocus() {
    return !this.is_new_record_to_edit()
  }
  // event_keypress(event?: KeyboardEvent) {
  //   console.log(event)
  //   if ($bw_vim_event_conform(event, {code: 'Enter', ctrlKey: null})) {
  //     this['object_host()'].commit_records()
  //   } else if ($bw_vim_event_conform(event, {code: 'Escape'})) {
  //     this['object_host()'].cancel_records()
  //   }
  // }
  vim_cmd() {
    const result = new VimDefCmd(
      new VimDefCmdItem(VimCmd.escape, (opt: VimCmdOpt, cmd: VimCmd) => {
        // this.rec_ids_selection_invert(!this.rec_ids_selection_invert())
        this['object_host()'].cancel_records()
      }),
      new VimDefCmdItem(VimCmd.enter, (opt: VimCmdOpt, cmd: VimCmd) => {
        this['object_host()'].commit_records()
      }),
    )
    return result
  }
  close_button_click( event? : any , force? : $mol_atom_force ) {
    if (!event) return
    this['object_host()'].cancel_records()
    // return ( event !== void 0 ) ? event : null as any
  }
}

