export class $bw_input_deep extends $.$bw_input_deep {
  @ $mol_mem
  Calendar() {
    const result : any = !this.dropped_down() ? null : super.Calendar()
    if (result) {
      result.shown(true)
      // shown_prop(result, true)
    }
    // console.log({result})
    return result
  }

  @ $mol_mem
  dropped_down( val? : any , force? : $mol_atom_force ) {
    // this.Calendar().shown(val, force)
    if (val !== void 0) {
      // console.log('dropped_down', {val})
      // console.trace()
    }
    return super.dropped_down(val, force)
    // return atom_prop({ obj: this, name: 'dropped_down', initial: false}, val) as boolean
  }

  @ $mol_mem
  deep_date() {
    const result = this.deep_moment().toString('YYYY-MM-DD')
    return result
  }
  @ $mol_mem
  day_shift(val? : number) {
     if (val !== void 0) {
       new $mol_defer(() => this.deep_value(-(val - 1)))
     }
     const result = -(this.deep_value() - 1)
     // const deep_value = this.deep_value()
     return result
  }
  @ $mol_mem
  deep_moment() {
    const result = this.current_moment().shift('P' +this.day_shift() + 'D')
    return result
  }
  @ $mol_mem_key
  current_moment_string(fmt : string = 'YYYY-MM-DD') {
    const result = new $mol_time_moment().toString(fmt)
    return result
  }
  @ $mol_mem
  current_moment() {
    const result = new $mol_time_moment(this.current_moment_string())
    return result
  }
  @ $mol_mem
  deep_date_text() {
    const result = 'с ' + this.deep_moment().toString('D Month YYYY')
    return result
  }
  @ $mol_mem
  input_text( val? : any , force? : $mol_atom_force ) {
    if (val !== void 0) {
      let deep_value = parseInt(val)
      if (!isNaN(deep_value)) {
        this.deep_value(deep_value)
      }
    }
    let result = this.deep_value()
    if (!this.input_focused()) {
      result = result + ' ' + plural_word(result, 'день', 'дня', 'дней')
    }
    return result as string
  }
}

// ==========================================================================

export class $bw_input_deep_calendar extends $.$bw_input_deep_calendar {
  @ $bw_false_on_outside_click
  shown( val? : any , force? : $mol_atom_force ) {
    return this['object_host()'].dropped_down(val, force)
  }
  deep_moment() {
    return this['object_host()'].deep_moment()
  }
  current_moment_string(fmt? : string) {
    return this['object_host()'].current_moment_string(fmt)
  }
  @ $mol_mem
  left_style() {
    let result : string | null
    const parent_width = this.parent_width()
    if (parent_width == 0) {
      result = null
    } else {
      const cur_width = this.cur_width() || this.calendar_width()
      result = (parent_width - cur_width) + "px"
    }
    return result
  }
  @ $mol_mem_key
  month_content(month : string) {
    const result : $mol_view[] =
      this.month_checkable(month) ?
        super.month_content(month) :
        [ this.month_text(month) ]
     return result
  }
  @ $mol_mem_key
  day_content( day : any ) {
    const result : $mol_view[] =
      this.day_checkable(day) ?
        super.day_content(day) :
        [ this.day_text(day) ]
    return result
  }
  @ $mol_mem_key
  month_checkable(month : string) {
    const result = month <= this.current_moment_string('YYYY-MM')
    return result
  }
  @ $mol_mem_key
  day_checkable(day : string) {
    const result = day <= this.current_moment_string() && !this.day_ghost(day)
    return result
  }
  day_shift_of_day(day : string) {
    const new_moment = new $mol_time_moment(day)
    const new_moment_string = new_moment.toString('YYYY-MM-DD')
    const interval_string = this.current_moment_string() + '/' + new_moment_string
    const time_interval = new $mol_time_interval(interval_string)
    const result = time_interval.duration.count('P1D')
    return result
  }
  @ $mol_mem_key
  day_click( day : any , val? : any , force? : $mol_atom_force ) {
    if (val !== void 0) {
      this.shown(false)
      this.day_shift(this.day_shift_of_day(day))
    }
    return ( val !== void 0 ) ? val : null as any
  }
  title() {
    const result = this.mode() ?  this.year_moment().toString('YYYY'): this.month_moment().toString('Month YYYY')
    return result
  }
  // @ $mol_mem
  // mode( val? : any , force? : $mol_atom_force ) {
    // const result = atom_prop(this, 'mode', true, val) as boolean
    // const result = atom_prop({ obj: this, name: 'mode', initial: false}, val) as boolean
    // console.log({mode: result, val})
    // return result
  // }
  @ $mol_mem
  mode_attr_value() {
    const result : string = this.mode() ? 'month' : 'day'
    return result
  }

  @ $mol_mem
  month_shift( val? : any , force? : $mol_atom_force ) {
    if (val !== undefined) {
      this.year_shift(0)
    }
    return super.month_shift(val)
    // const result = atom_prop({obj: this, name: 'month_shift', initial: 0}, val) as number
    // return result
  }

  // @ $mol_mem
  // year_shift( val? : any , force? : $mol_atom_force ) {
  //   const result = atom_prop({obj: this, name: 'year_shift', initial: 0}, val) as number
  //   console.log({year_shift: result, val})
  //   return result
  // }

  @ $mol_mem
  month_moment() {
    const moment = new $mol_time_moment(this.deep_moment().toString('YYYY-MM'))
    let result : $mol_time_moment = moment.shift('P' + this.month_shift() + 'M')
    return result
  }

  @ $mol_mem
  year_moment() {
    const moment = new $mol_time_moment(this.month_moment().toString('YYYY'))
    let result : $mol_time_moment = moment.shift('P' + this.year_shift() + 'Y')
    return result
  }
  prev_next_click(val : number) {
    new $mol_defer(() =>
      this.mode() ?
        this.year_shift(this.year_shift() + val) :
        this.month_shift(this.month_shift() + val)
    )
  }
  @ $mol_mem
  prev_click( val? : any , force? : $mol_atom_force ) {
    if (val !== void 0) {
      this.prev_next_click(-1)
    }
    return ( val !== void 0 ) ? val : null as any
  }
  @ $mol_mem
  next_click( val? : any , force? : $mol_atom_force ) {
    if (val !== void 0) {
      this.prev_next_click(+1)
    }
    return ( val !== void 0 ) ? val : null as any
  }
  calendar_content() {
    const result =
      this.mode() ?
        [this.Months()] :
        // [].concat( this.Weekdays() )
        [].concat( this.Weekdays(), this.Weeks() )
    return result
  }

  @ $mol_mem
  months_rows() {
    const result : $mol_view[] = []
    for( let i = 0; i < 6; ++i ) {
      result.push( this.MonthRow( i ) )
    }
    return result
  }

  @ $mol_mem_key
  month_cells(row : number ) {
    const result : $mol_view[] = []

    let start = new $mol_time_moment({ year : this.year_moment().year, month: 0 + row * 2 })

    for( let i = 0 ; i < 2 ; ++i ) {
      result.push( this.Month( start.shift({ month : i }).toString( 'YYYY-MM' ) ) )
    }
    return result
  }
  @ $mol_mem_key
  month_text(month : string) {
    const result = new $mol_time_moment( month ).toString( "Month" )
    return result
  }

  @ $mol_mem_key
  month_click( month : any , val? : any , force? : $mol_atom_force ) {
    if ( val !== void 0 ) {
      const new_moment = new $mol_time_moment(month)
      const interval_string = this.month_moment().toString('YYYY-MM') + '/' +  new_moment.toString('YYYY-MM')
      const time_interval = new $mol_time_interval(interval_string)
      const month_shift_delta = Math.round(time_interval.duration.count('P1M'))
      this.month_shift( this.month_shift() + month_shift_delta )
      this.mode(false)
    }
    return ( val !== void 0 ) ? val : null as any
  }

  @ $mol_mem_key
  day_selected(day : string) {
    const result = day == this.deep_moment().toString('YYYY-MM-DD')
    return result
  }

  @ $mol_mem_key
  month_selected( month : string ) {
    const result = month == this.month_moment().toString('YYYY-MM')
    return result
  }

  drop_filter_click( val? : any , force? : $mol_atom_force ) {
    if (val !== void 0) {
      this.day_shift(0)
      this.shown(false)
    }
    return ( val !== void 0 ) ? val : null as any
  }

  calendar_width() {
    return this.calendar_day_size() * 7
  }
}

